import React from 'react';
import PT from 'prop-types';
import styled from 'styled-components';
import ReactTable from 'react-table';
import shouldUpdate from 'recompose/shouldUpdate';

import 'react-table/react-table.css';

import buildColumns from './buildColumns';

import colors from '../../constants/styles/colors';
import font from '../../constants/styles/font';
import sizes from '../../constants/styles/sizes';

const textProps = {
  previousText: 'Anterior',
  nextText: 'Siguiente',
  loadingText: 'Cargando...',
  noDataText: 'No se encontraron filas',
  pageText: 'Página',
  ofText: 'de',
  rowsText: 'filas',
};

const StyledReactTable = styled(ReactTable)`
  .-pagination {
    .-btn {
      background: ${colors.primary};
      border-radius: 0;
      color: ${colors.white};
      font-size: ${font.sizes.xs};
      font-weight: 700;
      text-transform: uppercase;
      &:not([disabled]):hover {
        background: ${colors.primaryS1};
      }
    }
    .-pageInfo {
      font-size: ${font.sizes.xs};
      font-weight: 700;
    }
    .-pageJump {
      input {
        border: 1px solid ${colors.grayT5};
        border-radius: 0;
        color: ${colors.blackT2};
        font-size: ${font.sizes.sm};
        margin: 0 ${sizes.xs};
        padding: ${sizes.xxs};
      }
    }
    .-pageSizeOptions {
      position: relative;
      &:after {
        background: ${colors.primaryD3}
          url(data:image/svg+xml;utf8;base64,)
          center center no-repeat;
        border: 1px solid ${colors.primaryS4};
        content: '';
        display: inline-block;
        pointer-events: none;
        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        width: 26px;
      }
    }
    select {
      appearance: none;
      border: 1px solid ${colors.grayT5};
      border-radius: 0;
      color: ${colors.blackT2};
      font-size: ${font.sizes.xs};
      font-weight: 700;
      padding: ${sizes.sm};
      padding-right: ${sizes.lg};
    }
  }
`;

const DataTables = ({ data }) => {
  const columns = buildColumns(data);
  return (
    <StyledReactTable
      columns={columns}
      data={data}
      minRows={0}
      pageSizeOptions={[20, 50, 100, 250, 500, 1000]}
      {...textProps}
    />
  );
};

DataTables.propTypes = {
  data: PT.arrayOf(PT.object).isRequired,
};

const checkPropsChange = (props, nextProps) =>
  props.data.length !== nextProps.data.length;

export default shouldUpdate(checkPropsChange)(DataTables);



